<template>
  <el-drawer
    :center="true"
    :append-to-body="true"
    :close-on-press-escape="false"
    :wrapper-closable="false"
    :title="title"
    size="50"
    :visible="value"
    :destroy-on-close="true"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-form ref="form" :model="form" inline :rules="rules" label-width="120px">
      <el-scrollbar class="scroll-drawer">
        <el-form-item label="标签ID" prop="labelId">
          <el-input v-model.trim="form.labelId" placeholder="请输入" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="备注" prop="remarks">
          <el-input v-model.trim="form.remarks" placeholder="请输入" style="width: 178px;" />
        </el-form-item>
      </el-scrollbar>
      <div class="drawer-footer">
        <el-button :loading="loading" type="primary" @click="handleConfirm('form')">{{ loading ? '提交中 ...' : '确 定' }}</el-button>
        <el-button @click="beforeClose">取 消</el-button>
      </div>
    </el-form>
  </el-drawer>
</template>

<script>
import { addListApi as addApi, updateListApi as updataApi } from '@/apis/modules/warehouse/label'

const INIT_FORM = {
  labelId: '',
  remarks: ''
}

export default {
  components: {
  },
  props: {
    value: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    editType: {
      type: String,
      default: 'add'
    },
    dialogForm: {
      type: Object,
      default: () => {
        return { ...INIT_FORM }
      }
    }
  },
  data() {
    return {
      form: INIT_FORM,
      loading: false,
      rules: {
        labelId: [
          { required: true, message: '请输入标签ID', trigger: 'blur' }
        ]
        // remarks: [
        //   { required: true, message: '请输入备注', trigger: 'blur' }
        // ],
      },
      gradeOpts: []
    }
  },
  computed: {
  },
  watch: {
    dialogForm: {
      handler: function(value) {
        if (this.editType === 'add') {
          this.form = { ...INIT_FORM }
        } else {
          this.form = { ...value }
        }
      },
      immediate: true
    }
  },
  mounted() {
  },
  methods: {
    handleConfirm(formName) {
      const api = { modify: updataApi, add: addApi }[this.editType]
      const msg = { modify: '修改成功', add: '新增成功' }[this.editType]
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.loading = true
          api({
            ...this.form
          }).then(res => {
            const { code } = res
            if (!code) {
              this.$message({
                message: `${msg}`,
                type: 'success'
              })
              this.$emit('submit')
              this.beforeClose()
            }
            this.loading = false
          }).catch(err => {
            console.log(err)
            this.loading = false
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    initSelect() {
    },
    beforeClose(done) {
      this.$refs.form.resetFields()
      this.$emit('input', false)
      this.form = Object.assign({}, { ...INIT_FORM })
      typeof done === 'function' && done()
    }
  }
}
</script>

  <style lang='scss' scoped>
  .container{
    width: 860px;
    margin: 0 auto;
    .left{
      width: 640px;
    }
    .right{
      margin-left: 20px;
      width: 180px;
    }
  }
  ::v-deep:focus {
    outline: 0;
  }
  .scroll-drawer {
    height: calc(100vh - 150px);
    // overflow: auto;
  }
  ::v-deep .el-scrollbar .el-scrollbar__wrap {
    overflow-x: hidden;
  }
  ::v-deep .el-drawer {
    width: 640px;
  }
  .drawer-footer{
    text-align: right;
  }
  </style>

