<template>
  <div class="app-container">
    <!-- 头部搜索 -->
    <div>
      <div>
        <el-form ref="filterItem" :model="query" inline @submit.native.prevent>
          <el-row>
            <el-form-item label="按位置查询 : " prop="storageId">
              <el-link
                type="primary"
                style="min-width: 178px"
                @click="chooseLocation"
              >
                {{ query.storageId ? query.currentPosition : "请选择位置" }}
              </el-link>
            </el-form-item>
            <el-button v-if="query.storageId" type="danger" @click="clearLocation">清除</el-button>
          </el-row>
          <!-- <el-row>
            <el-form-item label="按位置查询 : " prop="storageId">
              <div class="control-x">
                <el-link
                  type="primary"
                  style="min-width: 178px"
                  @click="chooseLocation"
                >
                  {{ query.storageId ? query.currentPosition : "请选择位置" }}
                </el-link>
                <span v-if="query.storageId" class="x-suffix el-icon-circle-close" @click="clearLocation" />
              </div>
            </el-form-item>
          </el-row> -->
          <el-row>
            <el-form-item prop="labelId">
              <el-input v-model.trim="query.labelId" clearable placeholder="标签ID查询" style="width: 178px;" @keyup.enter.native="search" />
            </el-form-item>
            <el-form-item prop="batteryPower">
              <el-select
                v-model="query.batteryPower"
                filterable
                clearable
                placeholder="电量"
                style="width: 178px;"
              >
                <el-option
                  v-for="(item,index) in [{name: '电量充足', id: 'GOOD'},{name: '低电量', id: 'LOW'},{name: '更换电池', id: 'BAD'},]"
                  :key="index"
                  :label="item.name"
                  :value="item.id"
                />
              </el-select>
            </el-form-item>
            <el-form-item prop="connectionStatus">
              <el-select
                v-model="query.connectionStatus"
                filterable
                clearable
                placeholder="状态"
                style="width: 178px;"
              >
                <el-option
                  v-for="(item,index) in [{name: '在线', id: '在线'},{name: '离线', id: '离线'}]"
                  :key="index"
                  :label="item.name"
                  :value="item.id"
                />
              </el-select>
            </el-form-item>
            <el-form-item prop="warehouseSapId">
              <el-select
                v-model="query.warehouseSapId"
                filterable
                clearable
                placeholder="sap仓库"
                style="width: 178px;"
                @change="getChildNodeList('area')"
              >
                <el-option
                  v-for="item in sapWarehousePullList"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id"
                />
              </el-select>
            </el-form-item>
            <el-form-item prop="areaId">
              <el-select
                v-model="query.areaId"
                filterable
                clearable
                placeholder="区"
                style="width: 178px;"
                :disabled="!query.warehouseSapId"
                @change="getChildNodeList('shelves')"
              >
                <el-option
                  v-for="(item,index) in areaPullList"
                  :key="index"
                  :label="item.name"
                  :value="item.id"
                />
              </el-select>
            </el-form-item>
            <el-form-item prop="shelvesId">
              <el-select
                v-model="query.shelvesId"
                filterable
                clearable
                placeholder="架"
                style="width: 178px;"
                :disabled="!query.areaId"
              >
                <el-option
                  v-for="(item,index) in shelvesPullList"
                  :key="index"
                  :label="item.name"
                  :value="item.id"
                />
              </el-select>
            </el-form-item>
            <el-button type="success" icon="el-icon-search" @click="init">搜索</el-button>
            <el-button type="warning" icon="el-icon-refresh-left" @click="resetForm('filterItem')">重置</el-button>
            <el-button type="primary" icon="el-icon-plus" @click="handleAdd">新增</el-button>
            <el-button type="primary" icon="el-icon-refresh" @click="handleSync">同步</el-button>
            <el-button type="primary" icon="el-icon-refresh-right" @click="handleRefreshInkScreen">刷新油墨屏幕</el-button>
          </el-row>
        </el-form>
      </div>

      <div class="clearfix">
        <div class="fl" />
        <div class="fr" />
      </div>
    </div>

    <!-- <div class="clearfix top-filter">
      <el-button
        class="filter-item"
        type="primary"
        icon="el-icon-plus"
        @click="handleAdd"
      >新增</el-button>
    </div> -->
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :selection="false"
        :table-column-sort="true"
        :form-ident="formIdent"
        :table-data="tableData"
        :column-list="columnList"
        :expand-list="expandList"
        :operation-width="225"
        :refresh-key="refreshKey"
        @refresh="searchInit"
        @rowClick="handleRowClick"
        @expandChange="handleExpandChange"
      >
        <template slot-scope="scope">
          <el-button type="primary" :disabled="scope.row.connectionStatus === '离线' || !scope.row.storageId" @click="handleRefresh(scope.row)">刷新</el-button>
          <el-button type="warning" @click="handleModify(scope.row)">编辑</el-button>
          <el-button type="danger" @click="handleDelete(scope.row)">删除</el-button>
        </template>
      </table-com>
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />

    <!-- 新增 -->
    <edit-dialog
      v-model="dialogVisible"
      :dialog-form="dialogForm"
      :edit-type="editType"
      :title="dialogTitle"
      @submit="init"
    />

    <storage-location v-if="kuweiVisible" v-model="kuweiVisible" @confirm="selectedStorageLocation" />

  </div>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import { getListApi, deleteListApi, syncBatchApi, batchRefreshScreenApi, refreshApi, queryLastTaskInfoApi } from '@/apis/modules/warehouse/label'
import { getSapWarehouseListApi, getChildNodeListApi } from '@/apis/modules/base/baseWarehouseLocation'
import EditDialog from './Dialog'
import StorageLocation from './StorageLocation'
import tableHeadSet from '@/mixins/tableHeadSet'
import scan from '@/mixins/scan.js'

export const progressMap = {
  // 低电量
  'BAD': {
    percentage: 25,
    format: 'BAD',
    color: '#f56c6c'
  },
  // 电量不足
  'LOW': {
    percentage: 50,
    format: 'LOW',
    color: '#e6a23c'
  },
  // 电量充足
  'GOOD': {
    percentage: 100,
    format: 'GOOD',
    color: '#67c23a'
  }
}

export default {
  // name: 'WarehouseLabel',
  components: {
    EditDialog,
    StorageLocation
  },
  mixins: [paginationPage, tableHeadSet, scan],
  data() {
    return {
      title: this.$route.meta.title,
      loading: false,
      query: {
        storageId: '',
        currentPosition: '',
        labelId: '',
        batteryPower: '',
        connectionStatus: '',
        warehouseSapId: '', // sap仓库
        areaId: '', // 区
        shelvesId: '' // 架
      },
      columnSets: [
        // 电量
        {
          prop: 'batteryPowerStr',
          vHtml: true,
          formatter: (c) => {
            return `<span style="color: ${progressMap[c.batteryPower].color}">${c.batteryPowerStr}</span>`
          }
        },
        // 状态
        {
          prop: 'connectionStatus',
          vHtml: true,
          formatter: (c) => {
            const obj = {
              '离线': '#f56c6c',
              '在线': '#67c23a'
            }
            return `<span style="color: ${obj[c.connectionStatus]}">${c.connectionStatus}</span>`
          }
        }
      ],
      dialogForm: {},
      supplierPullList: [],
      editType: 'add',
      dialogVisible: false,
      kuweiVisible: false,
      sapWarehousePullList: [],
      areaPullList: [],
      shelvesPullList: [],
      // 拓展行的状态，可以是一个对象或数组，key为行的id，value为拓展状态
      expandStates: {},
      expandType: 'form'
    }
  },
  computed: {
    dialogTitle() {
      if (this.editType === 'add') {
        return `新增${this.title}信息`
      } else {
        return `编辑${this.title}信息`
      }
    }
  },
  mounted() {
    this.getSapWarehousePullList()
    this.$route.params.labelId && (this.query.labelId = this.$route.params.labelId)
    this.init()
  },
  methods: {
    init() {
      this.loading = true
      const params = {
        page: {
          ...this.pageInfo
        },
        ...this.query
      }
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { formIdent, formHeadSets, pageData } = data
            this.formIdent = formIdent
            this.columnList = this.initTableHeadMixins(formHeadSets)
            console.log('this.expandList: ', this.expandList)
            const { list, ...pageInfo } = pageData
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // 条码号回车查询
    search() {
      this.handlePublicFn({ labelId: this.query.labelId })
    },
    // 扫码结束
    scanEnd(data) {
      this.handlePublicFn(JSON.parse(data))
    },
    handlePublicFn(obj) {
      const arr = obj.labelId.split(':')
      this.query.labelId = arr.length > 1 ? arr[1] : arr[0]
      this.searchInit()
    },
    handleRowClick({ row, column, cell, event }) {
      console.log(row)
      if (column.label === '操作') {
        return false
      }
      this.rightObj = { ...row }
    },
    // 获取拓展行的状态
    isExpanded(row) {
      return this.expandStates[row.id] || false
    },
    // 设置拓展行的状态
    toggleExpand(row) {
      this.$set(this.expandStates, row.id, !this.isExpanded(row))
    },
    // 展开行回调
    handleExpandChange(row) {
      console.log(567890, row)
      this.toggleExpand(row)
      if (!this.expandStates[row.id]) return
      queryLastTaskInfoApi({ labelId: row.labelId })
        .then(res => {
          const { code, data } = res
          if (!code) {
            // 任务状态 任务类型 任务执行时间
            const { taskStatus_dictText, taskType_dictText, taskExecuteTime } = data || { taskStatus_dictText: '---', taskType_dictText: '---', taskExecuteTime: '---' }
            console.log('调用成功')
            row.taskStatus_dictText = taskStatus_dictText
            row.taskType_dictText = taskType_dictText
            row.taskExecuteTime = taskExecuteTime
          }
        })
        .catch(err => {
          console.log('错误信息：', err)
        })
    },
    refresh() {
      // 更新分页
      this.init('clean')
    },
    // 批量删除
    handleDelete(item) {
      this.$confirm('此操作将永久删除选中信息, 是否继续?', '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }).then(() => {
        deleteListApi({
          id: item.id
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$notify.success({
              title: '成功',
              message: '删除成功'
            })
            this.init()
          }
        })
      }).catch((e) => {
        console.log(e)
        this.$notify.info({
          title: '消息',
          message: '已取消删除'
        })
      })
    },
    handleRefresh(item) {
      return new Promise((resolve, reject) => {
        refreshApi({ storageId: item.storageId })
          .then(res => {
            const { code } = res
            if (!code) {
              this.$notify.success({
                title: '成功',
                message: '刷新成功'
              })
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    },
    handleAdd() {
      this.editType = 'add'
      this.dialogForm = {}
      this.dialogVisible = true
    },
    // 同步
    handleSync() {
      return new Promise((resolve, reject) => {
        syncBatchApi()
          .then(res => {
            const { code } = res
            if (!code) {
              this.$notify.success({
                title: '成功',
                message: '同步成功'
              })
              this.init()
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    },
    // 刷新油墨屏幕
    handleRefreshInkScreen() {
      let params = {}
      const { warehouseSapId, areaId, shelvesId } = this.query
      params = { warehouseSapId, areaId, shelvesId }
      // 所有的字段值都为空，就直接传{}
      const values = Object.values(params)
      if (values.every(c => !c)) {
        params = {}
      }
      return new Promise((resolve, reject) => {
        batchRefreshScreenApi(params)
          .then(res => {
            const { code } = res
            if (!code) {
              this.$notify.success({
                title: '成功',
                message: '刷新成功'
              })
              this.init()
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    },
    handleModify(item) {
      this.dialogForm = { ...item }
      this.editType = 'modify'
      this.dialogVisible = true
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.query.currentPosition = ''
      this.searchInit()
    },
    chooseLocation() {
      this.kuweiVisible = true
    },
    clearLocation() {
      this.query.currentPosition = ''
      this.query.storageId = ''
    },
    selectedStorageLocation({ currentPosition, storageId }) {
      console.log('12112', { currentPosition, storageId })
      this.query.currentPosition = currentPosition
      this.query.storageId = storageId
    },
    // sap仓库下拉数据
    getSapWarehousePullList() {
      return new Promise((resolve, reject) => {
        getSapWarehouseListApi()
          .then(res => {
            const { code, data } = res
            if (!code) {
              this.sapWarehousePullList = data || []
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    },
    // 获取区 架 下拉数据
    getChildNodeList(type = '') {
      let params = {}
      if (type === 'area') { // 获取区数据
        this.query.areaId = ''
        this.query.shelvesId = ''
        params = {
          warehouseSapId: this.query.warehouseSapId,
          warehouseId: this.sapWarehousePullList.find(item => item.id === this.query.warehouseSapId).warehouseSapId
        }
      }
      if (type === 'shelves') { // 获取架数据
        this.query.shelvesId = ''
        params = {
          warehouseSapId: this.query.warehouseSapId,
          warehouseId: this.sapWarehousePullList.find(item => item.id === this.query.warehouseSapId).warehouseSapId,
          parentId: this.query.areaId
        }
      }
      const values = Object.values(params)
      if (!values.every(c => c)) return
      return new Promise((resolve, reject) => {
        getChildNodeListApi(params)
          .then(res => {
            const { code, data } = res
            if (!code) {
              if (type === 'area') {
                this.areaPullList = data || []
              }
              if (type === 'shelves') {
                this.shelvesPullList = data || []
              }
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    }
  }
}
</script>

<style lang="scss" scoped>
::v-deep .el-link {
  text-align: center;
}
.x-suffix {
  display: none;
  color: #909399;
}
.control-x {
  position: relative;
  &:hover {
    .x-suffix {
      display: block;
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
    }
  }
}
</style>
